"use client"

import { useState, useEffect, useRef } from "react"
import { useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar"
import { Textarea } from "@/components/ui/textarea"
import { PlusCircle, Trash2, Globe, Eye, Music, Upload, Volume2, VolumeX } from "lucide-react"
import { motion } from "framer-motion"

export default function Home() {
  const [bioData, setBioData] = useState({
    username: "",
    avatar: "",
    bio: "",
    location: "",
    lastSeen: new Date().toISOString(),
    links: [{ title: "", url: "" }],
    backgroundMusic: "",
    backgroundColor: "#000000"
  })

  const [isPlaying, setIsPlaying] = useState(false)
  const [isDragging, setIsDragging] = useState(false)
  const audioRef = useRef<HTMLAudioElement>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const router = useRouter()

  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    const formData = new FormData()
    formData.append('file', file)

    try {
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData
      })

      if (!response.ok) throw new Error('Upload failed')

      const data = await response.json()
      setBioData({ ...bioData, avatar: data.url })
    } catch (error) {
      console.error('Erro ao fazer upload:', error)
    }
  }

  const toggleMusic = () => {
    if (audioRef.current) {
      if (isPlaying) {
        audioRef.current.pause()
      } else {
        audioRef.current.play()
      }
      setIsPlaying(!isPlaying)
    }
  }

  const handleMusicUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    const formData = new FormData()
    formData.append('file', file)

    try {
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData
      })

      if (!response.ok) throw new Error('Upload failed')

      const data = await response.json()
      setBioData({ ...bioData, backgroundMusic: data.url })
    } catch (error) {
      console.error('Erro ao fazer upload da música:', error)
    }
  }

  const saveBio = async () => {
    if (!bioData.username) {
      alert("Nome de usuário é obrigatório")
      return
    }
    try {
      const response = await fetch("/api/save-bio", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(bioData),
      })
      if (!response.ok) throw new Error("Falha ao salvar bio")
      
      // Redirect to preview page after successful save
      router.push(`/${bioData.username}`)
    } catch (error) {
      console.error("Erro ao salvar bio:", error)
    }
  }

  useEffect(() => {
    // Load saved bio if exists
    const loadBio = async () => {
      try {
        const response = await fetch("/api/save-bio")
        const data = await response.json()
        if (data.bios && data.bios.length > 0) {
          setBioData(data.bios[0])
        }
      } catch (error) {
        console.error("Erro ao carregar bio:", error)
      }
    }
    loadBio()
  }, [])

  const addLink = () => {
    setBioData({
      ...bioData,
      links: [...bioData.links, { title: "", url: "" }]
    })
  }

  const updateLink = (index: number, field: "title" | "url", value: string) => {
    const newLinks = [...bioData.links]
    newLinks[index] = { ...newLinks[index], [field]: value }
    setBioData({ ...bioData, links: newLinks })
  }

  const removeLink = (index: number) => {
    const newLinks = bioData.links.filter((_, i) => i !== index)
    setBioData({ ...bioData, links: newLinks })
  }

  return (
    <div className="min-h-screen flex items-center justify-center p-4" style={{
      background: `linear-gradient(to bottom right, ${bioData.backgroundColor}99, ${bioData.backgroundColor})`
    }}>
      {bioData.backgroundMusic && (
        <audio ref={audioRef} src={bioData.backgroundMusic} loop />
      )}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="w-full max-w-md"
      >
        <Card className="bg-black/40 border border-white/10 text-white backdrop-blur-sm shadow-xl">
          <div className="p-6 space-y-8">
            <div className="flex flex-col items-center gap-6">
              <Avatar className="w-24 h-24 ring-2 ring-purple-500/50 transition-all duration-300 hover:ring-4">
                <AvatarImage src={bioData.avatar || ""} />
                <AvatarFallback className="bg-purple-900">
                  {bioData.username.slice(0, 2).toUpperCase()}
                </AvatarFallback>
              </Avatar>
              
              <div className="w-full space-y-6">
                <div className="space-y-4">
                  <Input
                    placeholder="Nome de usuário"
                    value={bioData.username}
                    onChange={(e) => setBioData({ ...bioData, username: e.target.value })}
                    className="bg-black/50 border-white/10 focus:border-purple-500/50"
                  />
                  
                  <Textarea
                    placeholder="Sua biografia"
                    value={bioData.bio}
                    onChange={(e) => setBioData({ ...bioData, bio: e.target.value })}
                    className="bg-black/50 border-white/10 min-h-[100px] focus:border-purple-500/50"
                  />
                  
                  <div className="flex items-center gap-2">
                    <Globe className="w-4 h-4 text-purple-400" />
                    <Input
                      placeholder="Localização"
                      value={bioData.location}
                      onChange={(e) => setBioData({ ...bioData, location: e.target.value })}
                      className="bg-black/50 border-white/10 focus:border-purple-500/50"
                    />
                  </div>
                </div>

                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <h3 className="text-sm font-semibold text-purple-400">Cor de Fundo</h3>
                    <Input
                      type="color"
                      value={bioData.backgroundColor}
                      onChange={(e) => setBioData({ ...bioData, backgroundColor: e.target.value })}
                      className="w-12 h-8 p-0 border-none bg-transparent"
                    />
                  </div>

                  <div className="flex items-center justify-between">
                    <h3 className="text-sm font-semibold text-purple-400">Música de Fundo</h3>
                    <div className="flex items-center gap-2">
                      {bioData.backgroundMusic && (
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={toggleMusic}
                          className="text-purple-400 hover:text-purple-300"
                        >
                          {isPlaying ? <VolumeX className="w-4 h-4" /> : <Volume2 className="w-4 h-4" />}
                        </Button>
                      )}
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => document.getElementById('music-upload')?.click()}
                        className="text-purple-400 hover:text-purple-300"
                      >
                        <Music className="w-4 h-4" />
                      </Button>
                      <input
                        id="music-upload"
                        type="file"
                        accept="audio/*"
                        onChange={handleMusicUpload}
                        className="hidden"
                      />
                    </div>
                  </div>

                  <div className="space-y-4">
                    <div className="flex items-center justify-between">
                      <h3 className="text-sm font-semibold text-purple-400">Links Sociais</h3>
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={addLink}
                        className="text-purple-400 hover:text-purple-300"
                      >
                        <PlusCircle className="w-4 h-4" />
                      </Button>
                    </div>

                    {bioData.links.map((link, index) => (
                      <div key={index} className="flex gap-2">
                        <Input
                          placeholder="Título"
                          value={link.title}
                          onChange={(e) => updateLink(index, "title", e.target.value)}
                          className="bg-black/50 border-white/10 focus:border-purple-500/50"
                        />
                        <Input
                          placeholder="Link"
                          value={link.url}
                          onChange={(e) => updateLink(index, "url", e.target.value)}
                          className="bg-black/50 border-white/10 focus:border-purple-500/50"
                        />
                        <Button
                          variant="ghost"
                          size="icon"
                          onClick={() => removeLink(index)}
                          className="text-red-400 hover:text-red-300"
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    ))}
                  </div>
                </div>
              </div>

              <div className="w-full pt-6 space-y-3">
                <Button 
                  onClick={saveBio}
                  className="w-full bg-purple-600 hover:bg-purple-700 text-white transition-colors duration-300"
                >
                  Salvar Bio
                </Button>

                {bioData.username && (
                  <Button
                    variant="outline"
                    onClick={() => router.push(`/${bioData.username}`)}
                    className="w-full bg-black/50 border-white/10 hover:bg-purple-900/50 transition-colors"
                  >
                    <Eye className="w-4 h-4 mr-2" />
                    Visualizar Bio
                  </Button>
                )}
              </div>

              {bioData.lastSeen && (
                <p className="text-xs text-gray-400">
                  Última atualização: {new Date(bioData.lastSeen).toLocaleString('pt-BR')}
                </p>
              )}
            </div>
          </div>
        </Card>
      </motion.div>
    </div>
  )
}
